*{
  margin: 0;
  padding: 0;
}
body{
  background-color: $body-light-brown;
  
  
}
header{
  height: 60px;
  background-color: $header-dark-brown;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-shadow: 0 1px 8px #3f0f0f;
  padding-left: 20px;
  padding-right: 20px;

  #logo{
    // display:none;
    height: 60px;;
    width: 120px;
    background-color: transparent;
    font-family: 'Black And White Picture', sans-serif;
    text-shadow: 0 0 5px rgba(238, 234, 0, 0.2);
    color: $red-font;
    font-size: 28px;
    line-height: 60px;
    text-align: center;
  }

  #navigation{
    list-style: none;
    display: flex;
    
    width: 900px;

    li{
      // display: inline-block;
      margin-right: 40px;
      margin-left: 10px;
      padding-left: 10px;
      
      height: inherit;
      width: fit-content;
    
      .menu-link{
        text-decoration: none;
        color: $white-font;
        font-size: 36px;
        font-family: 'Peddana';
        // text-transform: uppercase;
        text-shadow: 0 0 10px $red-font;
        letter-spacing: .09em;
        line-height: 70px;
        font-weight: 800;

        transition: all 500ms;
        &:hover{
          color: $red-font;
        }

        
      }
      .currentPage{
        color: $red-font;
      }
    }
  }

  #user_display{
    display: flex;
    align-items: center;

    #user_display_options{
      height: 50px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: center;
      color: $white-font;

      #user_display_name{
        font-size: 20px;
        font-family: "Roboto";
      }

      .register_or_login{
        text-decoration: none;
        color: rgb(160, 60, 60);
        font-family: "Roboto";
        font-size: 14px;

        &:hover{
          font-weight: 700;
        }
      }
      
    }
    #user_avatar{
      height: 50px;
      width: 50px;
      border-radius: 50%;
      background-color: $red-font;
      margin: 5px;
    }
  }

}

main{
  height: 590px;
  width: 95%;
  padding: 20px;
  border: 1px solid $header-dark-brown;
  box-shadow: inset 0 0 2px $header-dark-brown;
  border-radius: 5px;
  margin: 15px auto;
  background-color: $main-brown;
  color: $white-font;
}