.cardBrowser__inputs{
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  width: 100%;
  height: 50px;
  // background-color: green;
  padding-bottom: 5px;
  border-bottom: 1px solid rgb(41, 10, 10);

  label{
    text-align: center;
    padding-left: 5px;
    padding-right: 20px;
  }

  i{
    font-size: 24px;
    margin: 0 3px;
    cursor: pointer;
    opacity: .3;
  }

  .mana-query-on{
    box-shadow: 0 0 6px $white-font;
    opacity: 1;
  }
}

.cardBrowser__output{
  overflow: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  height: 530px;
  padding: 5px;

  .card-image{
    height: 325px;
    width: 232px;
    border-radius: 14px;
    position: relative;
    background-size: cover;
    margin: 1px;
    cursor: pointer;
  }
}