// colors of font and background for listed cards

.R{
  background-image: linear-gradient(30deg, $red-card, adjust-color($red-card, 20, 20, 20,));
  border: 1px solid adjust-color($red-font, -30, -30, -30);
}
.G{
  background-image: linear-gradient(30deg, $green-card, adjust-color($green-card, 20, 20, 20));
  border: 1px solid adjust-color($green-card, -30, -30, -30);
}
.W{
  background-image: linear-gradient(30deg, $white-card, adjust-color($white-card, 15, 15, 15));
  border: 1px solid adjust-color($white-card, -30, -30, -30);
  color: #444 !important;
}
.B{
  background-image: linear-gradient(30deg, $black-card, adjust-color($black-card, 10, 10, 10));
  border: 1px solid adjust-color($black-card, 20, 20, 20);
}
.U{
  background-image: linear-gradient(30deg, $blue-card, adjust-color($blue-card, 20, 20, 20));
  border: 1px solid adjust-color($blue-card, -30, -30, -30);
}
.card-neutral{
  background-image: linear-gradient(30deg, $neutral-card, adjust-color($neutral-card, 20, 20, 20));
  border: 1px solid adjust-color($neutral-card, -30, -30, -30);
}

.BG{
  background-image: linear-gradient(30deg, $black-card, $green-card);
}
.BR{
  background-image: linear-gradient(30deg, $black-card, $red-card);
}
.BU{
  background-image: linear-gradient(30deg, $black-card, $blue-card);
}
.BW{
  background-image: linear-gradient(30deg, $black-card, $white-card);
}
.GR{
  background-image: linear-gradient(30deg, $green-card, $red-card);
}
.GU{
  background-image: linear-gradient(30deg, $green-card, $blue-card);
}
.GW{
  background-image: linear-gradient(30deg, $green-card, $white-card);
}
.RU{
  background-image: linear-gradient(30deg, $red-card, $blue-card);
}
.RW{
  background-image: linear-gradient(30deg, $red-card, $white-card);
}
.UW{
  background-image: linear-gradient(30deg, $blue-card, $white-card);
}
.multicolored{
  background-image: linear-gradient(70deg, rgba(18, 17, 22, .6), rgba(34, 83, 54, .6), rgba(148, 24, 24, .6), rgba(53, 88, 156, .6), rgba(230, 227, 216, .6));
}
.common{
  background: linear-gradient(#aaa, white);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.W .common, .card-neutral .common{
  background: linear-gradient(#1a1a1a, #545);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.uncommon{
  background: linear-gradient(#eee, #333);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.W .uncommon{
  background: radial-gradient(rgb(107, 105, 105), rgb(160, 160, 143), #333);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card-neutral .uncommon{
  background: linear-gradient(rgb(110, 103, 103), rgba(31, 31, 34, .8), rgb(140, 140, 155));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.card-neutral .rare, .W .rare{
  background: linear-gradient(#c81, rgb(255, 174, 0));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.rare{
  background: linear-gradient(#c81, rgb(255,205,0));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.mythic{
  background: linear-gradient(rgb(219, 124, 35), #cc3300);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}