.collection__container{
  display: flex;
  justify-content: space-between;
  font-family: 'Roboto';
  
  form{
    width: 200px;
  }
  .collection__list{
    list-style: none;
    width: 500px;
    height: 590px;
    overflow: auto;
    background-color: transparent;
    margin: 0 10px;
    padding-right: 10px;
    border-radius: 3px;

    .collection__list-item{
      color: $white-font;
      font-size: 20px;
      letter-spacing: .04em;
      padding: 2px 5px;
      border-radius: 3px;
      cursor: pointer;
      transition: all 1s;

      .card-count{
        float: right;
        font-size: 16px;
        margin-top: 3px;
      }

      &:hover{
        margin-left: 10px;
        margin-right: -10px;
        box-shadow: -3px 0 15px rgb(37, 16, 6);
      }
    }

  }
  .collection__card-details{
    box-sizing: border-box;
    padding: 3px;
    background-color: transparent;
    width: 50%;
    height: 590px;
    display: flex;
    justify-content: space-between;

    .collection__image{
      height: 460px;
      width: 330px;
      border-radius: 14px;
      position: relative;
      background-size: cover;
      margin: 1px;
    }
    .collection__card-stats{
      width: 100%;
      border-collapse: collapse;
      margin-left: 1px;

      tr:nth-child(even){
        background-color: adjust-color(transparent, 20, 20, 20); 
      }
      th, td{
        padding: 2px 2px;
      }

      th{
        text-align: left;
        width: 150px;
        font-size: 20px;
      }
      
      
    }
  }
}

